  background: #e8e8e8;
  border: 1px solid #e8e8e8;
  border-radius: 7px;
  gap: 8px;
  padding: 14px 56px;
}

.secondary:hover, .secondary:focus {
  color: #fff;
  background: #d14836;
  border: 1px solid #d14836;
  border-radius: 7px;
}

.other {
  color: #e8e8e8;
  width: auto;
  height: 43px;
  background: none;
  border: 1px solid #e8e8e8;
  border-radius: 7px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
  padding: 12px 32px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.18;
  display: flex;
}

.other:hover, .other:focus {
  color: #e8e8e8;
  background: #d14836;
  border-radius: 7px;
}

