// write-us

.write-us {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-top: $padding;
  padding-bottom: $padding;
  color: $white;
  &__title {
    font-family: "Unbounded";
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 1.1;
    color: $background;
    margin-bottom: 24px;
  }

  .form-wrap {
    display: block;
    @include desktop {
      display: flex;
      justify-content: space-between;
      margin-bottom: 16px;
    }
    .form-wrap-name {
      display: flex;
      flex-direction: column;
      @include mobile {
        margin-bottom: 16px;
      }
      &__label-name {
        font-family: "Unbounded";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 1;
        color: $background;
        margin-bottom: 6px;
      }
      &__field-name {
        background: $white;
        border: 1px solid $secondary;
        box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.08);
        border-radius: 3px;
        padding: 13px 12px;
      }
    }

    .form-wrap-number {
      display: flex;
      flex-direction: column;
      @include mobile {
        margin-bottom: 16px;
      }
      &__field-number {
        @include mobile {
          margin-top: 6px;
        }
        background: $white;
        border: 1px solid $secondary;
        box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.08);
        border-radius: 3px;
        padding: 13px 12px;
      }
    }
  }

  form {
    display: flex;
    flex-direction: column;
  }

  textarea {
    width: 100%;
    max-height: 100px;
    margin-top: 16px;
    @include desktop {
      background: $white;
      border: 1px solid $secondary;
      box-shadow: 0px 0px 4px 1px rgba(0, 0, 0, 0.08);
      border-radius: 3px;
    }
  }
}

.submit {
  margin-top: 32px;
}

.choose-messenger {
  margin-top: 10px;
  legend {
    margin-bottom: 10px;
  }
}

.icon_select_mate {
  position: absolute;
  top: 11px;
  right: 2%;
  font-size: 16px;
  height: 22px;
  transition: all 275ms;
}

.select_mate {
  position: relative;
  float: left;
  width: 100%;
  font-family: "Roboto";
  color: #777;
  font-weight: 300;
  background: $white;
  box-shadow: 1px 2px 10px -2px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  transition: all 375ms ease-in-out;
  padding: 13px 0;
  margin-bottom: 16px;
  /* Oculto el elemento select */
  select {
    position: absolute;
    overflow: hidden;
    height: 0px;
    opacity: 0;
    z-index: -1;
  }
}

.cont_list_select_mate {
  position: relative;
  float: left;
  width: 100%;
}

.cont_select_int {
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 999;
  overflow: hidden;
  height: 0px;
  width: 100%;
  background-color: #fff;
  padding: 0px;
  margin-bottom: 0px;
  margin-top: 0px;
  border-radius: 0px 0px 3px 3px;
  box-shadow: 1px 4px 10px -2px rgba(0, 0, 0, 0.2);
  transition: all 375ms ease-in-out;

  li {
    position: relative;
    float: left;
    width: 96%;
    border-bottom: 1px solid #e0e0e0;
    background-color: $white;
    list-style-type: none;
    padding: 10px 2%;
    margin: 0px;
    transition: all 275ms ease-in-out;
    display: block;
    cursor: pointer;

    &:last-child {
      border-radius: 3px;
      border-bottom: 0px;
    }
    &:hover {
      background-color: #eeeeee;
    }
  }

  .active {
    background-color: #eeeeee;
  }
}

.selecionado_opcion {
  padding-left: 10px;
  display: block;
  margin: 0px;
  cursor: pointer;
  color: #a7a7a7;
}

// radio

.choose-messenger {
  display: flex;
  flex-direction: column;
}

.choose-messenger [type="radio"]:checked,
.choose-messenger [type="radio"]:not(:checked) {
  position: absolute;
  left: -9999px;
}

.choose-messenger [type="radio"]:checked + .choose-messenger__label,
.choose-messenger [type="radio"]:not(:checked) + .choose-messenger__label {
  position: relative;
  padding: 6px 0 4px 38px;
  cursor: pointer;
  line-height: 20px;
  display: inline-block;
  margin-bottom: 4px;
}

.choose-messenger [type="radio"]:checked + .choose-messenger__label:before,
.choose-messenger [type="radio"]:not(:checked) + .choose-messenger__label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 7px;
  width: 20px;
  height: 20px;
  border: 2px solid $background;
  // border: 2px solid $primary;
  border-radius: 100%;
  background: transparent;
}

.choose-messenger [type="radio"]:checked + .choose-messenger__label:after,
.choose-messenger [type="radio"]:not(:checked) + .choose-messenger__label:after {
  content: "";
  width: 10px;
  height: 10px;
  top: 11.5px;
  left: 5px;
  background: $background;
  // background: $primary;
  position: absolute;
  border-radius: 100%;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.choose-messenger [type="radio"]:not(:checked) + .choose-messenger__label:after {
  opacity: 0;
  -webkit-transform: scale(0);
  transform: scale(0);
}

.choose-messenger [type="radio"]:checked + .choose-messenger__label:after {
  opacity: 1;
  -webkit-transform: scale(1);
  transform: scale(1);
}
