p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  padding: 0;
}

ul li {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  cursor: pointer;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: $white;
  cursor: pointer;
}

p {
  color: $white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $white;
}

.no-scroll {
  overflow-y: hidden;
}

img {
  display: block;
  max-width: 100%;
}
