.footer {
  display: block;
  position: relative;
  @include desktop {
    padding-top: 70px;
    padding-bottom: 66px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-top: 2px solid $background;
  }
}

.logo-footer {
  margin-bottom: 40px;
  @include desktop {
    margin-right: 142px;
  }
}

.icon-logo-footer {
  width: 108px;
  height: 139px;
}

// left

.wrap-left {
  &__list {
    display: flex;
    @include mobile {
      flex-direction: column;
      text-align: center;
    }

    @include desktop {
      display: flex;
    }
  }
}

.support {
  display: flex;
  flex-direction: column;
  color: $white;
  &__title {
    margin-bottom: 16px;
    font-family: "Unbounded";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    color: $background;
  }
}

.wrap-right {
  margin-top: 56px;
  margin-bottom: 112px;
  display: flex;
  @include desktop {
    display: flex;
    margin-left: 103px;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 47%;
      background: $background;
      width: 2px;
      height: 100%;
      display: block;
    }
  }
}

// right

.wrap-right-list {
  @include mobile {
    width: 100%;
  }
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 24px;
}

.wrap-right-list:nth-last-child(2) {
  margin-left: 58px;
}

.wrap-right-item {
  &__link {
    font-family: "Unbounded";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 1.2;
    color: $background;
    &:hover,
    &:focus {
      color: $accent;
    }
  }
}

// up

.wrap-up {
  position: relative;

  .up {
    position: fixed;
    bottom: 30px;
    right: 30px;
    fill: $white;
    width: auto;
    z-index: 99;
    border: none;
    background: transparent;
    @include desktop {
      display: none;
      position: absolute;
      bottom: 100px;
      right: 50%;
      cursor: pointer;
    }
    .up-text {
      font-family: "Unbounded";
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 1;
      color: $background;
      margin-right: 8px;
    }
  }
}

.up,
.up-text {
  &:hover {
    fill: $accent;
    color: $accent !important;
  }
}

.--pos {
  margin-top: 40px;
}

.copyright {
  display: none;
  @include desktop {
    margin-top: 40px;
  }
}

.social-media {
  display: none;
  @include desktop {
    margin-left: 68px;
  }
}
