@mixin mobile {
  @media (min-width: 0px) and (max-width: 768px) {
      @content;
  }
}
@mixin tablet {
  @media (min-width: 769px) and (max-width: 1240px) {
      @content;
  }
}
@mixin desktop {
  @media (min-width: 1241px) and (max-width: 2560px) {
      @content;
  }
}


